import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import vk_cofe from 'vkcoffee'

class Main extends React.Component{

    constructor(props){
        super(props)

        this.state={encode:"",
                    decode:""}
    }

    decode(params) {
        return vk_cofe.decrypt(params);
    }

    render() {
        return(
            <div className='Game'>
                <span>exemple:<br/>
                VK CO FF EE 56 72 37 53 6A 32 48 31 6D 46 2F 4D 54 74 42 78 30 47 46 48 41 69 30 74 34 36 65 7A 69 59 62 6E 6C 73 42 6E 4F 70 55 50 41 47 55 6A 4C 2F 4D 50 6D 58 43 62 6B 33 53 52 48 74 6C 71 41 33 4D 54 VK CO FF EE
                </span><br/>
                <textarea value={this.state.encode} onChange={(e)=> {

                this.setState(
                    {decode: this.decode(e.target.value),
                    encode: e.target.value}
                    )
                }} />

                {this.state.decode}
                
            </div>
        );
    }
}

ReactDOM.render(
    <Main />,
    document.getElementById('root')
  );